import _ from 'lodash'
import axios from 'axios'
import { notification } from 'ant-design-vue'
import { getToken } from './token'
import { getSessionStorageItem } from './sessionStorage'

export const createRequest = config => {
  const instance = axios.create(config)

  // 
  instance.interceptors.request.use(config => {
    if (config.useAuth !== false) {
      config.headers.auth = getToken()
    }
    return config
  }, error => {
    notification.error({
      message: error.message
    })
    return Promise.reject(error)
  })

  // 
  instance.interceptors.response.use(
    response => response.data,
    error => {
      const languageBase = getSessionStorageItem('language_base') || {}

      if (error.config.showError !== false) {
        const data = _.get(error, 'response.data').replace(/\n|\r$/, '')

        notification.error({
          message: _.get(error, 'response.status') || error.message,
          description: languageBase[data] || data
        })
      }
      return Promise.reject(error)
    }
  )

  return instance
}
