import _ from "lodash";
import { Modal } from "ant-design-vue";
import { getToken, setToken, rsaEncrypt, h, jwtDecoded } from "@/utils";
import { login, getRsa, register, userAccountExist } from "@/api";

const secret = "ZkiB6J7hjNECQx3z";
const encryptLogin = (data, pubKey) =>
  rsaEncrypt(
    {
      ..._.pick(data, ["account", "password"]),
      h: h(data.account, data.password)
    },
    pubKey
  );
const setAuth = ({ commit }, auth) =>
  new Promise((resolve, reject) => {
    if (setToken(auth)) {
      commit("setAuth", auth);
      resolve(true);
    } else {
      const options = {
        title: "error",
        content: "Token error"
      };
      Modal.error(options);
      reject(new Error(options.content));
    }
  });
const decodedPubKey = data =>
  _.get(jwtDecoded("." + data, secret), "payloadData.pub");

const state = {
  pubKey: "",
  auth: getToken(),
  signInLoading: false,
  signUnLoading: false,
  existingAccounts: {}
};

const mutations = {
  setPubKey(state, pubKey) {
    state.pubKey = pubKey;
  },
  setAuth(state, auth) {
    state.auth = auth;
  },
  setSignInLoading(state, signInLoading) {
    state.signInLoading = signInLoading;
  },
  setSignUpLoading(state, signUnLoading) {
    state.signUnLoading = signUnLoading;
  },
  addExistingAccount(state, data) {
    state.existingAccounts = {
      ...state.existingAccounts,
      ...data
    };
  }
};

const actions = {
  getPubKey({ commit, state }) {
    !state.pubKey &&
      getRsa().then(data => {
        const key = decodedPubKey(data);
        key
          ? commit("setPubKey", key)
          : Modal.error({
              title: "error",
              content: "Get rsa error"
            });
      });
  },
  login({ commit, state }, data) {
    commit("setSignInLoading", true);
    return login(encryptLogin(data, state.pubKey))
      .then(auth => setAuth({ commit }, auth))
      .finally(() => {
        commit("setSignInLoading", false);
      });
  },
  register({ commit, state }, data) {
    commit("setSignUpLoading", true);
    return register(encryptLogin(data, state.pubKey))
      .then(auth => setAuth({ commit }, auth))
      .finally(() => {
        commit("setSignUpLoading", false);
      });
  },
  accountExist({ commit, state }, account) {
    return _.isBoolean(state.existingAccounts[account])
      ? Promise.resolve(state.existingAccounts[account])
      : userAccountExist(account).then(res => {
          commit("addExistingAccount", { [account]: res });
          return res;
        });
  }
};

export default {
  namespaced: true,
  state,
  mutations,
  actions
};
