import CryptoJS from 'crypto-js'
import JSEncrypt from 'jsencrypt'
import _ from 'lodash'

export const crypto = _.cloneDeep(CryptoJS);

// base64url 
export const base64url = source => crypto.enc.Base64.stringify(source).replace(/=+$/, '').replace(/\+/g, '-').replace(/\//g, '_')

// base64url  Base64
export const base64urlToBase64 = str => {
  str = str.replace(/-/g, '+').replace(/_/g, '/')
  const pad = str.length % 4
  if (pad > 1) return str + new Array(5 - pad).join('=')
  return str
}

// jwt 
export const jwtDecoded = (token, secret) => {
  const tokenSplit = token.split('.')
  const jwt = {
    header: tokenSplit[0] || 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9',
    payload: tokenSplit[1],
    signature: tokenSplit[2]
  }
  if (tokenSplit.length !== 3 || base64url(crypto.HmacSHA256(`${jwt.header}.${jwt.payload}`, secret)) !== jwt.signature) return jwt
  try {
    jwt.payloadData = JSON.parse(crypto.enc.Utf8.stringify(crypto.enc.Base64.parse(base64urlToBase64(jwt.payload))))
  } catch (err) { window.console.error(err) }
  return jwt
}

// rsa 
export const rsaEncrypt = (value, pubKey) => {
  const encrypt = new JSEncrypt();

  if (_.isObject(value)) {
    value = JSON.stringify(value)
  }

  pubKey = crypto.enc.Utf8.stringify(crypto.enc.Base64.parse(pubKey))
  encrypt.setPublicKey(pubKey)

  let res = ''
  const limitLen = 117
  for (let i = 0; i < value.length; i += limitLen) {
    const str = encrypt.encrypt(value.slice(i, i + limitLen))
    if (!str) return str
    res += str
  }
  return res
}
