<template>
  <a-card
    :title="languageTable.user_login"
    :bodyStyle="{ padding: '1px 0 0' }"
    style="margin:20px"
  >
    <Table storeModule="user_login" :columns="columns" :params="params">
      <template v-slot:message="{ text }">
        {{ languageBase[text] || text }}
      </template>
    </Table>
  </a-card>
</template>

<script>
import { mapState } from "vuex";
import Table from "@/components/Table";
import { getTokenId } from "@/utils";

export default {
  components: {
    Table
  },
  data() {
    return {
      params: {
        u: getTokenId()
      },
      columns: [
        {
          dataIndex: "ip",
          sorter: true
        },
        {
          dataIndex: "country",
          sorter: true
        },
        {
          dataIndex: "message",
          scopedSlots: { customRender: "message" },
          sorter: true
        },
        {
          dataIndex: "created_at",
          scopedSlots: { customRender: "unix" },
          sorter: true
        }
      ]
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      languageTable: store => store.languageTable.values
    })
  },
  metaInfo() {
    return {
      title: this.languageTable.user_login
    };
  }
};
</script>
