<template>
  <a-row type="flex" justify="center" style="padding: 20px 10px">
    <a-card :bordered="false" hoverable>
      <a-form-model
        ref="ruleForm"
        :model="formData"
        :rules="rules"
        @submit="handleSubmit"
        @submit.native.prevent
        style="width:350px;max-width:80vw;"
      >
        <a-form-model-item>
          <a-row type="flex" justify="center">
            <h1>{{ languageBase.sign_up_for_oos }}</h1>
          </a-row>
        </a-form-model-item>
        <a-form-model-item prop="account">
          <a-input
            v-model="formData.account"
            :placeholder="languageTableColumn['user.account']"
          >
            <a-icon slot="prefix" type="user" style="color:rgba(0,0,0,.25)" />
          </a-input>
        </a-form-model-item>
        <a-form-model-item prop="password">
          <a-input
            v-model="formData.password"
            type="password"
            :placeholder="languageTableColumn['user.password']"
          >
            <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)" />
          </a-input>
        </a-form-model-item>
        <a-form-model-item prop="confirmPassword">
          <a-input
            v-model="formData.confirmPassword"
            type="password"
            :placeholder="languageBase.confirm_password"
          >
            <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)" />
          </a-input>
        </a-form-model-item>
        <a-form-model-item>
          <a-button
            block
            type="primary"
            html-type="submit"
            :loading="login.signUnLoading"
          >
            {{ languageBase.sign_up }}
          </a-button>
        </a-form-model-item>
      </a-form-model>

      <a-row type="flex" justify="end">
        <router-link :to="{ name: 'login', query: $route.query }">
          {{ languageBase.sign_in }}
        </router-link>
      </a-row>
    </a-card>
  </a-row>
</template>

<script>
import _ from "lodash";
import { mapState, mapActions } from "vuex";

export default {
  data() {
    const validateRequired = (rule, value, callback) => {
      !value ? callback(this.languageBase.please_input) : callback();
    };

    const validateAccount = (rule, value, callback) => {
      this.accountExist(value).then(res => {
        res ? callback(this.languageBase.account_exist) : callback();
      });
    };

    const validateConfirmPassword = (rule, value, callback) => {
      value && this.formData.password !== value
        ? callback(this.languageBase.passwords_inconsistent)
        : callback();
    };

    return {
      formData: {
        account: "",
        password: "",
        confirmPassword: ""
      },
      rules: {
        account: [
          { validator: validateRequired },
          { validator: validateAccount, trigger: "blur" }
        ],
        password: [
          { validator: validateRequired },
          { min: 6, max: 20, trigger: "blur" }
        ],
        confirmPassword: [
          { validator: validateRequired },
          { validator: validateConfirmPassword, trigger: "blur" }
        ]
      },
      loading: false
    };
  },
  computed: {
    ...mapState({
      login: store => store.login,
      languageBase: store => store.languageBase.values,
      languageTableColumn: store => store.languageTableColumn.values
    })
  },
  watch: {
    languageBase() {
      this.handleRules();
      this.$refs.ruleForm.clearValidate();
    },
    "formData.password"() {
      this.formData.confirmPassword &&
        this.$refs.ruleForm.validateField("confirmPassword");
    }
  },
  created() {
    this.login.auth ? this.handleRedirect() : this.getPubKey();
  },
  mounted() {
    this.handleRules();
  },
  methods: {
    ...mapActions("login", {
      getPubKey: "getPubKey",
      register: "register",
      accountExist: "accountExist"
    }),
    handleSubmit() {
      this.$refs.ruleForm.validate(valid => {
        valid &&
          this.register(this.formData).then(() => {
            this.$notification.success({
              message: this.languageBase.sign_up,
              description: this.languageBase.successful
            });

            this.handleRedirect();
          });
      });
    },
    handleRules() {
      this.rules.password[1].message = _.template(
        this.languageBase.character_template
      )(this.rules.password[1]);
    },
    handleRedirect() {
      !this.$route.query.redirect || /^http/.test(this.$route.query.redirect)
        ? this.$router.replace("/")
        : this.$router.replace(decodeURIComponent(this.$route.query.redirect));
    }
  },
  metaInfo() {
    return {
      title: this.languageBase.sign_up_account
    };
  }
};
</script>
