import { getToken, crypto } from "@/utils";
import { systemCCRequest } from "./request";

const h = sid => crypto.MD5(getToken() + systemCCRequest.k + sid).toString();

export const systemCCInit = sid =>
  systemCCRequest.post("system/init", { h: h(sid), sid });

export const systemCCStop = sid =>
  systemCCRequest.post("system/stop", { h: h(sid), sid });
