<template>
  <div style="padding:24px">
    <a-drawer
      :title="editDrawer.title"
      :maskClosable="false"
      :visible="editDrawer.visible"
      :width="500"
      @close="closeEditDrawer"
    >
      <Edit :formData="formData" @submitSuccess="closeEditDrawer" />
    </a-drawer>

    <a-row type="flex" justify="end" style="margin-bottom:20px">
      <a-button
        type="primary"
        @click="showEdit(languageBase.create_system, {})"
      >
        {{ languageBase.create_system }}
      </a-button>
    </a-row>

    <a-list
      :grid="{ gutter: 16, xs: 1, md: 2, xl: 3, xxl: 4 }"
      :data-source="list.data"
      :loading="list.loading"
    >
      <template v-slot:renderItem="item">
        <a-list-item v-if="!item.deleted_at">
          <a-card>
            <template v-slot:title>
              <a
                v-if="!!item.state"
                :target="userInfo.id !== item.user.id ? '_self' : '_blank'"
                :href="`/${item.id}?auth=${loginAuth}` | oosF"
                style="margin-right:8px"
                @contextmenu.prevent
              >
                <a-icon type="login" />
                {{ languageBase.sign_in }}
              </a>
              <span :title="item.name">
                {{ item.name }}
              </span>
            </template>
            <template v-slot:extra>
              <a-switch
                :checked-children="languageBase.enable"
                :un-checked-children="languageBase.disable"
                :checked="!!item.state"
                :loading="item.saveLoading"
                :disabled="userInfo.id !== item.user.id"
                @change="handleState(item)"
              />
            </template>
            {{ item.description }}
            <template v-slot:actions>
              <a
                v-if="userInfo.id === item.user.id && !!item.state"
                key="setting"
                :href="`/${item.id}?auth=${loginAuth}` | systemCCF"
                :title="languageBase.set_system"
                @contextmenu.prevent
              >
                <a-icon type="setting" />
              </a>
              <a-icon
                v-if="userInfo.id === item.user.id && !!item.state"
                key="edit"
                type="edit"
                :title="languageBase.editing_system"
                @click="showEdit(languageBase.editing_system, item)"
              />
              <a-popconfirm
                key="delete"
                :title="languageBase.sure_delete"
                @confirm="handleDelete(item)"
              >
                <a-icon :title="languageBase.delete_system" type="delete" />
              </a-popconfirm>
            </template>
          </a-card>
        </a-list-item>
      </template>
    </a-list>

    <a-row v-if="list.pagination.total" type="flex" justify="end">
      <a-pagination
        :current="pagination.current"
        :total="pagination.total"
        :pageSize="pagination.pageSize"
        :showSizeChanger="pagination.showSizeChanger"
        :showTotal="pagination.showTotal"
        @change="paginationChange"
        @showSizeChange="paginationChange"
      />
    </a-row>
  </div>
</template>

<script>
import _ from "lodash";
import { mapState, mapMutations, mapActions } from "vuex";
import Edit from "./Edit";
import { getTokenId } from "@/utils";
import { systemCCInit, systemCCStop } from "@/api/systemCC";
import { systemStop } from "@/api/system";

export default {
  name: "mySystemList",
  components: {
    Edit
  },
  data() {
    return {
      editDrawer: {
        title: "",
        visible: false
      },
      formData: {}
    };
  },
  computed: {
    ...mapState({
      loginAuth: store => store.login.auth,
      userInfo: store => store.user.detail.data,
      languageBase: store => store.languageBase.values,
      list: store => store.user_system.list
    }),
    pagination() {
      return (
        this.list.pagination && {
          ...this.list.pagination,
          showTotal: total =>
            _.template(this.languageBase.total_template)({ total })
        }
      );
    }
  },
  created() {
    this.fetchList({ current: 1, pageSize: 10 });
  },
  methods: {
    ...mapMutations("user_system", {
      setItem: "setItem"
    }),
    ...mapActions("user_system", {
      getList: "getList",
      save: "save"
    }),
    showEdit(title, formData) {
      this.formData = { ...formData };
      this.editDrawer.title = title;
      this.editDrawer.visible = true;
    },
    closeEditDrawer() {
      this.editDrawer.visible = false;
    },
    handleState(item) {
      if (!item.init) {
        this.setItem({ id: item.id, saveLoading: true });
        this.$message.info({
          content: this.languageBase.system_init_prompt
        });
        systemCCInit(item.id)
          .then(() => {
            this.save({ id: item.id, state: true, init: true }).then(() =>
              this.handleStateSaveSuccess(item.name, true)
            );
          })
          .catch(() => {
            this.setItem({ id: item.id, saveLoading: false });
          });
      } else {
        if (item.state) {
          systemStop(item.id);
          systemCCStop(item.id);
        }

        this.save({ id: item.id, state: !item.state }).then(() =>
          this.handleStateSaveSuccess(item.name, !item.state)
        );
      }
    },
    handleStateSaveSuccess(name, state) {
      this.$notification.success({
        message: `${this.languageBase.system}: ${name}`,
        description: state
          ? this.languageBase.successfully_started
          : this.languageBase.successfully_stopped
      });
    },
    handleDelete(item) {
      if (item.user.id === this.userInfo.id) {
        const del = () => {
          this.save({ id: item.id, deleted_at: 1 }).then(() => {
            this.handleDeleteSuceess();
          });
        };

        item.state ? systemCCStop(item.id).then(del) : del();
      }
    },
    handleDeleteSuceess() {
      this.$notification.success({
        message: this.languageBase.delete_system,
        description: this.languageBase.successful
      });
    },
    fetchList(params) {
      params.table = { filters: [[["members", "=", getTokenId()]]] };
      this.getList(params);
    },
    paginationChange(current, pageSize) {
      this.fetchList({ current, pageSize });
    }
  },
  metaInfo() {
    return {
      title: this.languageBase.my_system
    };
  }
};
</script>
