import { storeOptions } from "./default";
import { queryAll, save } from "@/api";

const tableName = "user";
const options = new storeOptions({
  detail: {
    api: queryAll,
    params: {
      table: {
        name: tableName,
        no_default_field: true,
        fields: ["id", "avatar", "name", "signature"]
      }
    }
  },
  save: {
    api: save,
    tableName,
    success({ commit, state }, userInfo) {
      commit("setData", {
        ...state.detail,
        type: "detail",
        data: [{ ...state.detail.data, ...userInfo }]
      });
    }
  }
});

export default {
  namespaced: true,
  ...options
};
