<template>
  <TableBase
    :tableName="storeModule"
    :columns="columns"
    :list="list"
    @change="handleChange"
    @state-change="handleStateChange"
  >
    <template
      v-for="(item, key) in $scopedSlots"
      v-slot:[key]="{ text, record, index }"
    >
      <span :key="key">
        <slot
          :name="key"
          v-bind:text="text"
          v-bind:record="record"
          v-bind:index="index"
        />
      </span>
    </template>
  </TableBase>
</template>

<script>
import _ from "lodash";
import { mapState } from "vuex";
import TableBase from "./TableBase";

export default {
  components: {
    TableBase
  },
  props: {
    storeModule: String,
    columns: Array,
    params: Object
  },
  computed: {
    ...mapState({
      list(store) {
        return store[this.storeModule].list;
      }
    }),
    getListActionName() {
      return this.storeModule + "/getList";
    },
    saveActionName() {
      return this.storeModule + "/save";
    }
  },
  watch: {
    params() {
      this.getList({ current: 1, pageSize: 10 });
    }
  },
  created() {
    this.getList({ current: 1, pageSize: 10 });
  },
  methods: {
    handleStateChange(record) {
      this.$store.dispatch(this.saveActionName, {
        id: record.id,
        state: !record.state
      });
    },
    getList(params) {
      params = _.mergeWith(_.cloneDeep(this.params), params);

      if (_.isEmpty(params.table)) {
        params.table = { order_by: [["id", "DESC"]] };
      }

      this.$store.dispatch(this.getListActionName, params);
    },
    handleChange(pagination, filters, sorter) {
      const params = _.pick(pagination, ["current", "pageSize"]);

      if (!_.isEmpty(sorter) && sorter.order) {
        params.table = {
          order_by: [[sorter.field, sorter.order === "ascend" ? "ASC" : "DESC"]]
        };
      }

      this.getList(params);
    }
  }
};
</script>
