import Vue from "vue";
import VueRouter from "vue-router";
import { getToken } from "@/utils";
import MySystem from "@/views/mySystem/List.vue";
import SignIn from "@/views/login/SignIn.vue";
import Register from "@/views/login/SignUp.vue";
import LoginList from "@/views/login/List.vue";
import User from "@/views/user/Index.vue";
import UserInfo from "@/views/user/Info.vue";
import UserPassword from "@/views/user/Password.vue";
import Logout from "@/components/Logout.vue";
import Page404 from "@/views/404.vue";
import Download from "@/views/Download.vue";

Vue.use(VueRouter);

const routes = [
  {
    name: "mySystem",
    path: "/",
    component: MySystem
  },
  {
    name: "login",
    path: "/login",
    component: SignIn
  },
  {
    name: "register",
    path: "/register",
    component: Register
  },
  {
    name: "loginList",
    path: "/loginList",
    component: LoginList
  },
  {
    name: "user",
    path: "/user",
    component: User,
    redirect: "/user/info",
    children: [
      {
        name: "userInfo",
        path: "/user/info",
        component: UserInfo
      },
      {
        name: "userPassword",
        path: "/user/password",
        component: UserPassword
      }
    ]
  },
  {
    name: "download",
    path: "/download/:filename/:hash",
    component: Download
  },
  {
    name: "logout",
    path: "/logout",
    component: Logout
  },
  {
    name: "404",
    path: "*",
    component: Page404
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

router.beforeEach((to, from, next) => {
  if (!getToken() && !/^(login|register|logout)$/.test(to.name)) {
    router.replace({
      name: "login",
      query: { redirect: encodeURIComponent(to.fullPath) }
    });
  } else {
    next();
  }
});

export default router;
