<template>
  <a-upload
    :action="action"
    :show-upload-list="false"
    :before-upload="beforeUpload"
    :data="handleData"
    @change="handleChange"
    accept="image/jpeg, image/jpg, image/png"
  >
    <a-avatar v-show="loading" :size="100" icon="loading" />
    <template v-if="!loading">
      <a-avatar v-if="value" :size="100" :src="value | image" />
      <a-avatar v-else :size="100" icon="user" />
    </template>
  </a-upload>
</template>

<script>
import { fileH } from "@/utils";
import { origins } from "@/dict";
import { handleImage } from "./utils";

export default {
  name: "upload-avatar",
  props: {
    value: String
  },
  data() {
    return {
      action: `${origins.OSS}/upload`,
      loading: false
    };
  },
  methods: {
    beforeUpload(file) {
      if (
        ["image/jpeg", "image/jpg", "image/png", "image/webp"].indexOf(
          file.type
        ) === -1
      ) {
        this.$message.error("jpgjpgpng");
        return false;
      }

      return handleImage({ file, width: 100, height: 100 }).then(file => {
        if (file.status === "done") {
          this.$emit("input", file.hash);
          return Promise.reject();
        }
        return file;
      });
    },
    handleData(file) {
      return {
        h: file.hash,
        s: fileH(file.hash).toString(),
        public: "1"
      };
    },
    handleChange({ file }) {
      if (file.status === "done") {
        this.$emit("input", file.hash);
      }
      this.loading = file.status === "uploading";
    }
  }
};
</script>
