import moment from 'moment'

let TOKEN_KEY = 'token'
const getPayload = token => {
  let payload = {}
  try {
    payload = JSON.parse(atob(token.replace(/(.+)\..+/, '$1')))
  } catch (err) {
    window.console.error(err)
  }
  return payload
}

export const setTokenKey = key => {
  TOKEN_KEY = key
}

// 
export const verifyToken = token => {
  const payload = getPayload(token)
  return payload.exp && payload.exp > moment().unix() + 1800
}

export const getTokenId = () => {
  const token = getToken()
  return token ? getPayload(token).id : 0
}

export const getToken = () => {
  const token = localStorage.getItem(TOKEN_KEY)

  return !token || !verifyToken(token) ? '' : token
}

export const setToken = token => {
  if (!verifyToken(token)) return false

  localStorage.setItem(TOKEN_KEY, token)
  return true
}

export const delToken = () => localStorage.removeItem(TOKEN_KEY)
