<template>
  <a-row
    type="flex"
    justify="center"
    :style="{
      flexGrow: 1
    }"
  >
    <a-form-model
      ref="ruleForm"
      :model="formData"
      :rules="rules"
      v-bind="formItemLayout"
      :style="{
        width: '768px',
        maxWidth: '100%'
      }"
      @submit="handleSubmit"
      @submit.native.prevent
    >
      <a-form-model-item :label="languageTableColumn['user.avatar']">
        <Avatar v-model="formData.avatar" />
      </a-form-model-item>
      <a-form-model-item prop="name" :label="languageTableColumn['user.name']">
        <a-input v-model="formData.name" />
      </a-form-model-item>
      <a-form-model-item :label="languageTableColumn['user.signature']">
        <a-textarea
          v-model="formData.signature"
          :autoSize="{ minRows: 3, maxRows: 6 }"
        />
      </a-form-model-item>
      <a-form-model-item
        :wrapper-col="{ md: { span: 20, offset: 4 }, span: 24 }"
      >
        <a-button
          block
          type="primary"
          html-type="submit"
          :loading="loading"
          :disabled="disabledSubmit"
        >
          {{ languageBase.confirm }}
        </a-button>
      </a-form-model-item>
    </a-form-model>
  </a-row>
</template>

<script>
import _ from "lodash";
import { mapState, mapActions } from "vuex";
import { getTokenId } from "@/utils";
import { userNameExist } from "@/api";
import Avatar from "@/components/upload/Avatar";

const usernameExist = async name => {
  if (usernameExist.names[name] === undefined) {
    usernameExist.names[name] = await userNameExist(name);
  }
};
usernameExist.names = {};

export default {
  components: {
    Avatar
  },
  data() {
    const validateName = (rule, value, callback) => {
      if (this.formData.name && this.formData.name !== this.userInfo.name) {
        usernameExist(value).finally(() => {
          usernameExist.names[value]
            ? callback(this.languageBase.username_exist)
            : callback();
        });
      } else {
        callback();
      }
    };

    return {
      formItemLayout: {
        labelCol: { md: { span: 4 } },
        wrapperCol: { md: { span: 20 } }
      },
      formData: {
        avatar: "",
        name: "",
        signature: ""
      },
      rules: {
        name: [{ validator: validateName, trigger: "blur" }]
      }
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      languageTableColumn: store => store.languageTableColumn.values,
      userInfo: store => store.user.detail.data,
      loading: store => store.user.detail.loading
    }),
    disabledSubmit() {
      return _.isEmpty(
        _.pickBy(this.formData, (value, key) => this.userInfo[key] !== value)
      );
    }
  },
  watch: {
    userInfo() {
      this.initFormData();
    }
  },
  mounted() {
    this.initFormData();
  },
  methods: {
    ...mapActions("user", {
      save: "save"
    }),
    initFormData() {
      _.forEach(["avatar", "name", "signature"], key => {
        this.formData[key] = this.userInfo[key];
      });
    },
    handleSubmit() {
      this.$refs.ruleForm.validate(valid => {
        valid &&
          this.save({
            ..._.pickBy(
              this.formData,
              (value, key) => this.userInfo[key] !== value
            ),
            id: getTokenId()
          }).then(() => {
            this.$notification.success({
              message: this.languageBase.save,
              description: this.languageBase.successful
            });
          });
      });
    }
  },
  metaInfo() {
    return {
      title: this.languageBase.my_info
    };
  }
};
</script>
