<template>
  <a-dropdown placement="bottomCenter">
    <a class="ant-dropdown-link" style="margin:0 20px;line-height: 32px">
      <a-row type="flex" align="middle">
        <template>
          <a-avatar v-if="userInfo.avatar" :src="userInfo.avatar | image" />
          <a-avatar v-else icon="user" style="background: #1890ff;" />
        </template>
        <span style="margin-left:8px">
          {{ userInfo.name }}
        </span>
      </a-row>
    </a>

    <a-menu slot="overlay">
      <a-menu-item>
        <router-link to="/user" style="padding:8px 40px">
          <a-icon type="user" />
          {{ languageBase.personal_center }}
        </router-link>
      </a-menu-item>
      <a-menu-item>
        <router-link to="/loginList" style="padding:8px 40px">
          <a-icon type="login" />
          {{ languageTable.user_login }}
        </router-link>
      </a-menu-item>
      <a-menu-divider />
      <a-menu-item>
        <a-button block type="link" icon="logout" size="small" @click="logout">
          {{ languageBase.logout }}
        </a-button>
      </a-menu-item>
    </a-menu>
  </a-dropdown>
</template>

<script>
import { mapState, mapMutations, mapActions } from "vuex";
import { delToken, getTokenId } from "@/utils";

export default {
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      languageTable: store => store.languageTable.values,
      userInfo: store => store.user.detail.data
    })
  },
  beforeMount() {
    this.getUserInfo({ table: { filters: [[["id", "=", getTokenId()]]] } });
  },
  methods: {
    ...mapMutations("login", {
      setLoginAuth: "setAuth"
    }),
    ...mapActions("user", {
      getUserInfo: "getDetail"
    }),
    logout() {
      delToken();
      this.setLoginAuth();
      this.$router.replace({
        name: "login",
        query: { redirect: encodeURIComponent(this.$route.fullPath) }
      });
    }
  }
};
</script>
