<template>
  <a-card style="margin:20px">
    <a-row type="flex" justify="center">
      <a-spin v-if="loading" />
      <a-result v-else-if="href" status="success">
        {{
          localeCode === "zh_CN"
            ? ""
            : "If your download does not begin,"
        }}
        <a-button
          type="link"
          icon="download"
          @click="handleDownload"
          style="padding:0"
        >
          {{
            localeCode === "zh_CN"
              ? ""
              : "please click here to try again"
          }}
        </a-button>
      </a-result>
      <a-result v-else status="error" :title="errorMessage" />
    </a-row>
  </a-card>
</template>

<script>
import _ from "lodash";
import { mapState } from "vuex";
import { fileIsExist, getFileAuth } from "@/api";
import { origins } from "@/dict";

export default {
  data() {
    return {
      loading: false,
      errorMessage: "",
      href: ""
    };
  },
  computed: {
    ...mapState({
      localeCode: store => store.locale.selected.code,
      languageBase: store => store.languageBase.values
    })
  },
  mounted() {
    this.getUrl();
  },
  methods: {
    getUrl() {
      this.errorMessage = "";
      const { hash, filename } = this.$route.params;

      if (!/^[0-9a-z]{32}$/.test(hash)) {
        this.errorMessage = "hash error";
        return;
      }

      this.loading = true;
      fileIsExist({ h: hash })
        .then(val => {
          if (!val) {
            this.loading = false;
            this.errorMessage = this.languageBase.file_not_exist;
            return;
          }

          getFileAuth()
            .then(auth => {
              this.href = `${origins.OSS}/${hash.replace(
                /^(.{2})(.{2})/,
                "$1/$2/"
              )}?auth=${auth}`;

              if (filename) {
                this.href += `&filename=${filename}`;
              }

              setTimeout(this.handleDownload, 100);
            })
            .finally(() => {
              this.loading = false;
            });
        })
        .catch(error => {
          this.loading = false;
          this.errorMessage = _.get(error, "response.data") || error.message;
        });
    },
    handleDownload() {
      const link = document.createElement("a");
      link.href = this.href;
      link.download = this.$route.params.filename;
      link.click();
    }
  },
  metaInfo() {
    return {
      title: this.languageBase.download
    };
  }
};
</script>
