<template>
  <a-row
    type="flex"
    justify="center"
    :style="{
      flexGrow: 1
    }"
  >
    <a-form-model
      ref="ruleForm"
      :model="formData"
      :rules="rules"
      @submit="handleSubmit"
      @submit.native.prevent
      :style="{
        width: '300px',
        maxWidth: '100%'
      }"
    >
      <a-form-model-item prop="old_password">
        <a-input
          v-model="formData.old_password"
          type="password"
          :placeholder="languageBase.p_e_o_l_p"
        >
          <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)" />
        </a-input>
      </a-form-model-item>
      <a-form-model-item prop="password">
        <a-input
          v-model="formData.password"
          type="password"
          :placeholder="languageBase.p_e_n_l_p"
        >
          <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)" />
        </a-input>
      </a-form-model-item>
      <a-form-model-item prop="confirmPassword">
        <a-input
          v-model="formData.confirmPassword"
          type="password"
          :placeholder="languageBase.p_e_c_n_l_p"
        >
          <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)" />
        </a-input>
      </a-form-model-item>
      <a-form-model-item>
        <a-button block type="primary" html-type="submit" :loading="loading">
          {{ languageBase.confirm }}
        </a-button>
      </a-form-model-item>
    </a-form-model>
  </a-row>
</template>

<script>
import _ from "lodash";
import { mapState, mapActions } from "vuex";
import { rsaEncrypt, h } from "@/utils";
import { passwordUpdate } from "@/api";

export default {
  data() {
    const validateConfirmPassword = (rule, value, callback) => {
      value && this.formData.password !== value
        ? callback(this.languageBase.passwords_inconsistent)
        : callback();
    };
    const validatePassword = (rule, value, callback) => {
      value && this.formData.old_password === value
        ? callback(this.languageBase.n_p_cannot_same_o_p)
        : callback();
    };

    return {
      formData: {
        old_password: "",
        password: "",
        confirmPassword: ""
      },
      rules: {
        old_password: [{ required: true }],
        password: [
          { required: true },
          { min: 6, max: 20, trigger: "blur" },
          { validator: validatePassword, trigger: "blur" }
        ],
        confirmPassword: [
          { required: true },
          { validator: validateConfirmPassword, trigger: "blur" }
        ]
      },
      loading: false
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      pubKey: store => store.login.pubKey
    })
  },
  watch: {
    languageBase() {
      this.handleRules();
      this.$refs.ruleForm.clearValidate();
    }
  },
  mounted() {
    this.handleRules();
    this.getPubKey();
  },
  methods: {
    ...mapActions("login", {
      getPubKey: "getPubKey"
    }),
    handleSubmit() {
      if (this.loading) return;

      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          this.loading = true;

          passwordUpdate(
            rsaEncrypt(
              {
                ..._.pick(this.formData, ["old_password", "password"]),
                h: h(this.formData.old_password, this.formData.password)
              },
              this.pubKey
            )
          )
            .then(() => {
              this.$notification.success({
                message: this.languageBase.save,
                description: this.languageBase.successful
              });
              this.$refs.ruleForm.resetFields();
            })
            .finally(() => {
              this.loading = false;
            });
        }
      });
    },
    handleRules() {
      this.rules.old_password[0].message = this.languageBase.p_e_o_l_p;
      this.rules.password[0].message = this.languageBase.p_e_n_l_p;
      this.rules.confirmPassword[0].message = this.languageBase.p_e_c_n_l_p;
      this.rules.password[1].message = _.template(
        this.languageBase.character_template
      )(this.rules.password[1]);
    }
  },
  metaInfo() {
    return {
      title: this.languageBase.change_password
    };
  }
};
</script>
