<template>
  <a-row type="flex" justify="center" style="padding:20px 10px">
    <a-card :bordered="false" hoverable>
      <a-form-model
        ref="ruleForm"
        :model="formData"
        :rules="rules"
        @submit="handleSubmit"
        @submit.native.prevent
        style="width:350px;max-width:80vw;"
      >
        <a-form-model-item>
          <a-row type="flex" justify="center">
            <h1>{{ languageBase.sign_in_to_oos }}</h1>
          </a-row>
        </a-form-model-item>
        <a-form-model-item prop="account">
          <a-input
            v-model="formData.account"
            :placeholder="languageTableColumn['user.account']"
          >
            <a-icon slot="prefix" type="user" style="color:rgba(0,0,0,.25)" />
          </a-input>
        </a-form-model-item>
        <a-form-model-item prop="password">
          <a-input
            v-model="formData.password"
            type="password"
            :placeholder="languageTableColumn['user.password']"
          >
            <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)" />
          </a-input>
        </a-form-model-item>
        <a-form-model-item>
          <a-button
            block
            type="primary"
            html-type="submit"
            :loading="login.signInLoading"
          >
            {{ languageBase.sign_in }}
          </a-button>
        </a-form-model-item>
      </a-form-model>

      <a-row type="flex" justify="end">
        <router-link :to="{ name: 'register', query: $route.query }">
          {{ languageBase.sign_up_account }}
        </router-link>
      </a-row>
    </a-card>
  </a-row>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  data() {
    const validateRequired = (rule, value, callback) => {
      !value ? callback(this.languageBase.please_input) : callback();
    };

    return {
      formData: {
        account: "",
        password: ""
      },
      rules: {
        account: [{ validator: validateRequired }],
        password: [{ validator: validateRequired }]
      }
    };
  },
  computed: {
    ...mapState({
      login: store => store.login,
      languageBase: store => store.languageBase.values,
      languageTableColumn: store => store.languageTableColumn.values
    })
  },
  created() {
    this.login.auth ? this.handleRedirect() : this.getPubKey();
  },
  methods: {
    ...mapActions("login", {
      getPubKey: "getPubKey",
      handleLogin: "login"
    }),
    handleSubmit() {
      this.$refs.ruleForm.validate(valid => {
        valid &&
          this.handleLogin(this.formData).then(() => {
            this.$notification.success({
              message: this.languageBase.sign_in,
              description: this.languageBase.successful
            });
            this.handleRedirect();
          });
      });
    },
    handleRedirect() {
      const redirect = decodeURIComponent(this.$route.query.redirect || "/");
      if (!/^http/.test(redirect)) {
        this.$router.replace(decodeURIComponent(redirect));
      } else {
        window.location.replace(
          !/\?/.test(redirect)
            ? `${redirect}?auth=${this.login.auth}`
            : `${redirect}&auth=${this.login.auth}`
        );
      }
    }
  },
  metaInfo() {
    return {
      title: this.languageBase.sign_in
    };
  }
};
</script>
