import { fileIsExist } from "@/api";
import { md5File, compressImage } from "@/utils";

// { file, width, height, quality }
export const handleImage = params =>
  new Promise((resolve, reject) => {
    // 
    compressImage(params)
      .then(blob => {
        if (blob.size > 1 << 20) {
          reject(new Error("1MB"));
        } else {
          md5File(blob)
            .then(hash => {
              // 
              fileIsExist({ h: hash, public: "1" })
                .then(res => {
                  if (res) {
                    blob.status = "done";
                  }

                  blob.hash = hash;
                  resolve(blob);
                })
                .catch(reject);
            })
            .catch(reject);
        }
      })
      .catch(reject);
  });
