import { crypto } from './crypto'

// (blobSlice)
export const md5File = file => new Promise((resolve, reject) => {
  const reader = new FileReader()
  reader.readAsArrayBuffer(file)
  reader.onload = () => resolve(crypto.MD5(crypto.lib.WordArray.create(reader.result)).toString())
  reader.onerror = e => reject(e)
})

// hash
export const fileH = s => crypto.MD5("tn0Zc7YrEFmLp56o" + s + "0n9M1KrAQ84t6lsH").toString()

// 
let s = ''
const m = s => crypto.MD5(s).toString()
export const slk = key => {
  s = key
}
export const h = (a, b) => m(a + s + b)