<template>
  <a-form-model
    ref="ruleForm"
    :model="formData"
    :rules="rules"
    @submit="handleSubmit"
    @submit.native.prevent
  >
    <a-form-model-item
      prop="name"
      :label="languageTableColumn['user_system.name']"
    >
      <a-input v-model="formData.name" />
    </a-form-model-item>
    <a-form-model-item :label="languageBase.description">
      <a-textarea v-model="formData.description" />
    </a-form-model-item>
    <a-form-model-item>
      <a-button
        block
        type="primary"
        html-type="submit"
        :loading="submitLoding"
        :disabled="disabledSubmit"
      >
        {{ languageBase.confirm }}
      </a-button>
    </a-form-model-item>
  </a-form-model>
</template>

<script>
import _ from "lodash";
import { mapState, mapActions } from "vuex";
import { getTokenId } from "@/utils";

export default {
  props: {
    formData: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    const validateRequired = (rule, value, callback) => {
      !value ? callback(this.languageBase.please_input) : callback();
    };

    return {
      submitLoding: false,
      rules: {
        name: [
          { required: true, validator: validateRequired },
          { min: 1, max: 60, trigger: "blur" }
        ]
      },
      oldFormData: {
        name: this.formData.name,
        description: this.formData.description
      }
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      languageTableColumn: store => store.languageTableColumn.values
    }),
    disabledSubmit() {
      return _.isEmpty(
        _.pickBy(this.oldFormData, (value, key) => this.formData[key] !== value)
      );
    }
  },
  watch: {
    languageBase() {
      this.handleRules();
      this.$refs.ruleForm.clearValidate();
    },
    "formData.id"() {
      this.oldFormData.name = this.formData.name;
      this.oldFormData.description = this.formData.description;
      this.$refs.ruleForm.clearValidate();
    }
  },
  mounted() {
    this.handleRules();
  },
  methods: {
    ...mapActions("user_system", {
      save: "save"
    }),
    handleSubmit() {
      this.$refs.ruleForm.validate(valid => {
        if (!valid) return;

        let formData = _.pick(this.formData, ["id", "name", "description"]);
        formData = _.pickBy(
          formData,
          (value, key) => value !== this.oldFormData[key]
        );

        if (_.isEmpty(formData)) return;

        if (!formData.id) {
          formData = {
            ...formData,
            members: [getTokenId()],
            state: false,
            init: false
          };
        }

        this.save(formData).then(() => {
          this.$notification.success({
            message: this.languageBase.save,
            description: this.languageBase.successful
          });
          this.$emit("submitSuccess");
        });
      });
    },
    handleRules() {
      const compiled = _.template(this.languageBase.character_template);
      this.rules.name[1].message = compiled(this.rules.name[1]);
    }
  }
};
</script>
