import { request } from "./request";
export * from "./query";
export * from "./file";

export const userAccountExist = account =>
  request.get("/user/account/exist", { params: { account } });

export const userNameExist = name =>
  request.get("/user/name/exist", { params: { name } });

export const getRsa = () => request.get("/rsa");
export const login = data => request.post("/login", { data });
export const register = data => request.post("/register", { data });
export const passwordUpdate = data =>
  request.post("/password/update", { data });

export const getFileAuth = () => request.get("/file/auth");
