<template>
  <a-table
    :columns="tableColumns"
    :data-source="list.data"
    :loading="list.loading"
    :pagination="pagination"
    :scroll="{ x: true }"
    rowKey="id"
    class="table-base"
    @change="handleChange"
  >
    <span v-if="!$scopedSlots.state" slot="state" slot-scope="text, record">
      <a-switch
        :checked-children="languageBase.enable"
        :un-checked-children="languageBase.disable"
        :disabled="record.auth === 1"
        :checked="!!text"
        :loading="record.saveLoading"
        @change="$emit('state-change', record)"
      />
    </span>

    <span v-if="!$scopedSlots.unix" slot="unix" slot-scope="text">
      {{ text | unixFormat }}
    </span>

    <!-- eslint-disable-next-line -->
    <template v-for="(item, key) in $scopedSlots" v-slot:[key]="text, record, index">
      <span :key="key">
        <slot
          :name="key"
          v-bind:text="text"
          v-bind:record="record"
          v-bind:index="index"
        />
      </span>
    </template>
  </a-table>
</template>

<script>
import _ from "lodash";
import { mapState } from "vuex";

export default {
  props: {
    columns: Array,
    list: Object,
    tableName: String
  },
  data() {
    return {
      tableColumns: []
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      languageTable: store => store.languageTable.values,
      languageTableColumn: store => store.languageTableColumn.values
    }),
    pagination() {
      return (
        this.list.pagination && {
          ...this.list.pagination,
          showTotal: total =>
            _.template(this.languageBase.total_template)({ total })
        }
      );
    }
  },
  watch: {
    languageBase() {
      this.handleTableColumns();
    },
    languageTable() {
      this.handleTableColumns();
    },
    languageTableColumn() {
      this.handleTableColumns();
    },
    list() {
      this.handleTableColumns();
    }
  },
  mounted() {
    this.handleTableColumns();
  },
  methods: {
    handleTableColumns() {
      if (
        !_.isEmpty(this.languageBase) &&
        !_.isEmpty(this.languageTable) &&
        !_.isEmpty(this.languageTableColumn)
      ) {
        const tableColumns = _.map(this.columns, item => ({
          ...item,
          key: item.key || item.dataIndex
        }));

        this.tableColumns = _.map(tableColumns, item => {
          let title = item.title || item.key;
          if (
            /^(id|state|created_at|updated_at|operation|show|comment|message)$/.test(
              item.key
            )
          ) {
            title = this.languageBase[item.key] || title;
          } else if (/\./.test(item.key)) {
            title = this.getLTableColumnTitle(...item.key.split(".")) || title;
          } else {
            title =
              this.languageTableColumn[`${this.tableName}.${item.key}`] ||
              title;
          }

          return { ...item, title };
        });
      }
    },
    getLTableColumnTitle(tableName, tableColumnName) {
      const table = this.languageTable[tableName];
      const tableColumn = this.languageTableColumn[
        `${tableName}.${tableColumnName}`
      ];
      return table && tableColumn ? `${table} - ${tableColumn}` : "";
    },
    handleChange(pagination, filters, sorter) {
      this.$emit("change", pagination, filters, sorter);
    }
  }
};
</script>

<style>
.table-base td {
  white-space: nowrap;
}
.table-base th {
  white-space: nowrap;
}
</style>
