<template>
  <div>
    logout
  </div>
</template>

<script>
import _ from "lodash";
import { mapMutations } from "vuex";
import { delToken } from "@/utils";

export default {
  created() {
    delToken();
    this.setLoginAuth();
    this.$router.replace({
      ..._.pick(this.$route, ["query", "params"]),
      name: "login"
    });
  },
  methods: {
    ...mapMutations("login", {
      setLoginAuth: "setAuth"
    })
  }
};
</script>
